import React from 'react';
import styled from 'styled-components';

const HeaderWrapper = styled.div`
  width: 100vw;
  height: 72px;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 144px 0 72px;
  box-sizing: border-box;
`;

const HeaderLogo = styled.div`
  width: 61px;
  height: 19px;
  background-image: url('/static/bland.svg');
  background-size: cover;
  background-position: center;
`;

const HeaderContact = styled.a`
  height: 40px;
  font-size: 1rem;
  font-weight: 500;
  color: #fff;
  text-decoration: none;
  padding: 0 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;

  &::before {
    content: "";
    height: 100%;
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    background: #0f0f0f;
    z-index: -1;
  }

  &::after {
    height: 100%;
    width: 100%;
    background: #9f9f9f;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
  }

  &::before:hover {
    width: 0;
  }
`;

const MenuToggle = styled.div`
  width: 72px;
  height: 72px;
  position: fixed;
  right: 72px;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const Header = ({handleMenuToggle}) => (
  <HeaderWrapper className="header">
    <HeaderLogo className="header__logo" />
    <HeaderContact href="" className="header__contact">
      Contact
    </HeaderContact>
    <MenuToggle>
      x
    </MenuToggle>
  </HeaderWrapper>
);

export default Header;



